import { BASE_IMAGE_URL,goodsDetail,jiaruCart } from '@/utils/api'
import uParse from '@/components/uParse/src/wxParse.vue'

export default {
	components: {
		uParse
	},
	data() {
		return {
			itemList: [],
			scrollWidth: 0,
			swipeHeight: 0,
			htmlString: '',
			item: {},
			showModal: false,
			goodsImg: '',
			ct_count: 1,
			type: 1,
			detail: ''
		}
	},
	methods: {
		imageLoad: function(e){
		  let imgWidth = e.mp.detail.width;
		  let imgHeight = e.mp.detail.height;
		  this.swipeHeight = parseFloat(imgHeight*this.scrollWidth/imgWidth)+"px";
		},
		inputChange(e){
			if(this.ct_count >= parseInt(this.item.g_kucun)){
				this.ct_count = this.item.g_kucun;
			}
		},
		async jiaruCart(){
			let params = {
				ct_openid: uni.getStorageSync("openid"),
				ct_g_id: this.item.g_id,
				ct_count: this.ct_count
			};
			let res = await jiaruCart(params);
			if(res.code == 1000){
				uni.showToast({
				  icon: 'none',
				  title: '添加成功',
				  duration: 1000
				});
				this.showModal = false;
			}else{
				uni.showToast({
				  icon: 'none',
				  title: '加入购物车失败',
				  duration: 1000
				});
			}
		},
		showDialog(index){
			let userInfo = uni.getStorageSync("userInfo");
			if(userInfo.m_role == 0){
				uni.showModal({
					title: "提示",
					content: "无权浏览，如需使用请联系客服开通权限",
					showCancel: false,
					success(res) {
						if(res.confirm){
						}
					}
				})
				return;
			}
					
			if(this.showModal){
				if(index == 1){
					//加入购物车
					this.jiaruCart();
				}else{
					//立即购买
					this.item.ct_count = this.ct_count;
					uni.setStorageSync("itemList", [this.item]);
					uni.navigateTo({
						url: '/pages/post_order/post_order'
					});
				}
				
			}else{
				this.showModal = true;
			}
			
		},
		closeModal(){
			this.showModal = false;
		},
		reduceAction(){
			if(this.ct_count == 1){
				return;
			}
			this.ct_count--;
		},
		addAction(){
			if(this.ct_count >= parseInt(this.item.g_kucun)){
				return;
			}
			this.ct_count++;
		},
		
		goToShoppingCard(){
			uni.switchTab({
				url: '/pages/tabbar/card/card'
			});
		},
		goToHome(){
			uni.switchTab({
				url: '/pages/tabbar/home/home'
			});
		},
		async goodsDetail(g_id){
			let res = await goodsDetail(g_id);
			if(res.code == 1000){
				this.item = res.data;
				// this.item.g_img_list.map((item) => {
				// 	if(item.img){
				// 		this.goodsImg = item.img;
				// 	}
				// });
				this.goodsImg = this.item.g_img;
				
				this.detail = res.data.g_detail;
				console.log(this.item.detail);
				var richtext=  this.detail;
				const regex = new RegExp('<img', 'gi');
				richtext= richtext.replace(regex, `<img style="max-width: 100%!important;height: auto;"`);
				this.detail = richtext;
				console.log(this.detail);
			}
		}
	},
	onLoad(options) {
		let that = this;
		uni.getSystemInfo({
		  success: function(res) {
			that.scrollWidth = res.windowWidth;
		  },
		});
		this.goodsDetail(options.g_id);
	}
}
